
@use '../abstracts' as *;
/**
 * Basic typography style for copy text
 */



// utility classes pertaining fonts

._text-center-md {
    @include respond(md) {
      text-align: center;
    }
  }

.main-heading {
    font-size: 2.6rem;
    font-weight: 400;
    letter-spacing: 0.3rem;
    display: inline-block;
    text-align: center;
    width: 100%;
    margin: 3rem 0;
    &::after {
      display: block;
      height: 2px;
      background-color: $brand-color;
      content: ' ';
      width: 100px;
      margin: 0 auto;
      margin-top: 0px;
      margin-top: 30px;
    }
  }
.lineHeight{
  line-height: 4rem;
}
.paragraph_title {
  font-size: 1.8rem;
  font-weight: 400;
  letter-spacing: 0.3rem;
  line-height: 2.6rem;
  margin: 1rem 0;
}

.paragraph_text {
  font-size: 1.5rem;
  font-weight: 300;
  line-height: 3rem;
  margin: 1rem 0;
  @include respond(md) {
    text-align: center;
    margin-bottom: 2rem;
  }
}