@use '../abstracts' as *;

.img-container {
  @include respond(md) {
    width: 100%;
  }
    img {
      width: 100%;
      object-fit: contain;
      display: block;
    }
  }
  
  .img-cover {
    @include respond(md) {
      width: 100%;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }

img{
    border-radius: .25rem;
}

.height-32 {
  height: 32rem;
}

.height-40 {
  height: 40rem;
}
.height-50 {
  height: 50.5rem;
}

.Visible{
  display: none;
  @include respond(md) {
    display: block;
    height: 0;
  }

}
.notVisible{
  display: inline-block;
  @include respond(md) {
    display: none;
  }

}





.wrapper.spaced__wrapper{
  margin-top: 5rem ;
}