@use '../abstracts' as *;

.navBar {
  position: fixed;
  top: 0;
  background-color: $white;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: 0.6s;
  padding: 1rem;
  font-size: 1.4rem;
  box-shadow: 5px 5px 5px rgba(22, 22, 22, 0.2);
  z-index: 10000;

  &__logo {
    width: clamp(6rem, 6vw, 12rem);
    @supports not (width: clamp(6rem, 6vw, 12rem)) {
      width: 30%;
    }
    cursor: pointer;
    &-svg {
      fill: $brand-color;
    }
    z-index: 100;
  }

  &__menu {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    @include respond(lg) {
      display: none;
    }

    & li {
      position: relative;
      list-style: none;

      & a {
        position: relative;
        display: inline-block;
        padding: 0 1rem;
        text-decoration: none;
        color: $brand-color;
        letter-spacing: 2px;
        font-size: 1.3rem;
        font-weight: 400;
        transition: all 0.4s ease-in-out;
        &:hover {
          color: $brand-secondary;
        }
        &:after {
          content: '';
          /* This is necessary for the pseudo element to work. */
          display: block;
          /* This will put the pseudo element on its own line. */
          margin: 0 auto;
          /* This will center the border. */
          width: 80%;
          /* Change this to whatever width you want. */
          padding-top: 0.3rem;
          /* This creates some space between the element and the border. */
          border-bottom: 2px solid $brand-secondary;
          opacity: 0;
          transition: all 0.6s ease-in-out;
        }

        &:hover:after {
          content: '';
          opacity: 100;
        }
      }
    }
  }
}

// ! Changing the menu when open with X
// !changing from open to active
.navBar__menu.active {
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: $brand-color;
  transition: all 0.5s ease-in-out;

  & li {
    margin-bottom: 3rem;

    & a {
      position: relative;
      font-size: 1.6rem;
      text-decoration: none;
      color: $white;
      letter-spacing: 2px;
      font-weight: 300;
      transition: 0.6s;
      transition: all 0.5s ease-in-out;

      &:hover {
        color: $white;
        font-weight: 600;
        font-size: 3.3rem;
        @include respond(sm) {
          font-size: 2.2rem;
        }
      }
    }
  }
}

// ! Sticky navBar start
.navBar.sticky {
  padding: 1rem;
  background-color: $white;
  
}

// ! Sticky navBar end

// ! Menu burger start
.navBar__toggler {
  position: relative;
  display: none;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  height: 3rem;
  width: 3rem;
  transition: all 0.5s ease-in-out;
  @include respond(lg) {
    display: flex;
  }

  &-burger {
    cursor: pointer;
    height: 2px;
    width: 2rem;
    border-radius: 5px;
    background-color: $brand-color;
    box-shadow: 0 2px 5x rgba(255, 101, 47, 0.2);
    transition: all 0.5s ease-in-out;

    &::before,
    &::after {
      content: '';
      position: absolute;
      height: 2px;
      width: 2rem;
      border-radius: 5px;
      background-color: $brand-color;
      box-shadow: 0 2px 5x rgba(255, 101, 47, 0.2);
      transition: all 0.5s ease-in-out;
    }

    &::before {
      transform: translateY(-0.5rem);
    }

    &::after {
      transform: translateY(0.5rem);
    }
  }
}

// ! Menu burger end
// ? Animation X start
.navBar__toggler {
  &-burger.open {
    transform: translateX(-50px);
    background: transparent;
    box-shadow: none;

    &.open::before {
      transform: rotate(45deg) translate(35px, -35px);
      background-color: $white;
    }

    &.open::after {
      transform: rotate(-45deg) translate(35px, 35px);
      background-color: $white;
    }
  }
}
// ? Animation X end

.navBar__logo.active {
  padding: 1rem;
  width: clamp(10rem, 10vw, 14rem);
  @supports not (width: clamp(6rem, 6vw, 12rem)) {
    width: 30%;
  }
  cursor: pointer;
 
  z-index: 100;
}

.navBar__logo-svg.active{
  fill: $white;
}