@use '../abstracts' as *;

footer {
  padding: 2rem;
  width: 100%;
  background-color: $brand-color;
  color: white;
  display: grid;
  grid-template-columns: 25% 50% 25%;
  grid-template-rows: repeat(2, 1fr);
  grid-template-areas:
    'infoDetail     .     social  '
    'infoDetail copyRight social';
  @include respond(md) {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

.infoDetail {
  display: flex;
  line-height: 2rem;
  padding: 0 0.5rem;
  flex-direction: column;
  justify-content: space-evenly;
  grid-area: infoDetail;
  margin-left: 4rem;
  
  @include respond(md) {
    width: 100%;
    margin-left: 0;
    font-size: 1.2rem;
    text-align: center;
    margin-bottom: .6rem;
  }
}

.copyRight {
  align-self: end;
  display: flex;
  flex-direction: column;
  justify-content: end;
  align-items: center;
  grid-area: copyRight;
  @include respond(md) {
    align-self: center;
    order: 1;
    margin-top: .6rem;
  }

  & a {
    display: inline-block;
    cursor: pointer;
    text-decoration: none;
    color: $mid-grey;
    font-weight: 600;
  }
}

.social {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  grid-area: social;
}

.svgContainer {
  width: 4rem;
  height: 4rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.svg-social {
  fill: $white;
  width: 1.6rem;
}

.svgContainer.phone {
  transition: all 0.4s ease-in-out;
  &:hover {
    background-color: $brand-secondary;
    border-radius:50%;
  }
}

.svgContainer.email {
  transition: all 0.4s ease-in-out;
  &:hover {
    background-color: #3abbdb;
    border-radius:50%;
  }
}
.svgContainer.insta  {
  transition: all 0.4s ease-in-out;
  &:hover {
    background-color: #e4405f;
    border-radius:50%;
  }
}
.svgContainer.facebook {
  transition: all 0.4s ease-in-out;
  &:hover {
    background-color: #3b5999;
    border-radius:50%;
  }
}
