@use '../abstracts' as *;
.four-o-four{
    min-height: 100vh;
    width: 100%;
    background-image: linear-gradient(to left bottom, #00ad9f, #299d87, #378d71, #3e7d5e, #406d4d);
    display: flex;
    justify-content: center;
        align-items: center;

    &__main{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

    }
}

.para-404{
