@use '../abstracts' as *;

.hero {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 100vh;

  background-image: linear-gradient(
      to top right,
      rgba(0, 0, 0, 0.192),
      rgba(0, 0, 0, 0.5)
    ),
    url('/src/assets/images/home/hero.jpg?size=2048');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 25%;
  background-attachment: fixed;

  @include respond(md) {
    background-image: linear-gradient(
        to top right,
        rgba(0, 0, 0, 0.192),
        rgba(0, 0, 0, 0.5)
      ),
      url('/src/assets/images/home/hero_cropped.jpg?size=1200');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50% 25%;
    background-attachment: fixed;
  }

  &__logo {
    width: clamp(30%, 45vw, 40rem);
    &-svg {
      fill: $white;
    }
    @include respond(md) {
      margin-bottom: 5rem;
    }
  }

  &__info {
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: $white;
    text-align: center;

    h1 {
      font-size: 3rem;
      font-weight: 400;
      letter-spacing: 0.3rem;
      line-height: 3rem;
      text-align: center;

      @include respond(md) {
        line-height: 5rem;
      }
    }

    h2 {
      font-size: 2.4rem;
      font-weight: 400;
      letter-spacing: 0.3rem;
      line-height: 3rem;

      @include respond(md) {
        display: none;
        line-height: 2rem;
      }
    }
    .textActive {
      @include respond(sm) {
        display: none;
      }
    }
  }
}

@supports (-webkit-touch-callout: none) {
  .hero {
    background-image: linear-gradient(
        to top right,
        rgba(0, 0, 0, 0.192),
        rgba(0, 0, 0, 0.5)
      ),
      url('/src/assets/images/home/hero.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50% 25%;
    background-attachment: scroll;
  }
}

