@use '../abstracts' as *;

.upholstery-hero {
  width: 100vw;
  height: 32vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(
      to top right,
      rgba(0, 0, 0, 0.192),
      rgba(0, 0, 0, 0.5),
    ),
    url('/src/assets/images/upholstery/upholstery_hero.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;

  
