  &__logo{
    margin-top: 7rem;
    width: clamp(30%, 45vw, 40rem);
    &-svg {
      fill: $white;
    }
    @include respond(md) {
      margin-bottom: 5rem;
    }
}

&__info{
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: $white;
  text-align: center;
 

  h1{
    font-size: 3rem;
    font-weight: 400;                    
    letter-spacing: .3rem;
    line-height: 3rem;
    text-align: center;
    
    @include respond(md) {
      line-height: 5rem;
    }
    
  }

  
  h2{
    font-size: 2.4rem;
    font-weight: 300;
    letter-spacing: .3rem;
    line-height: 3rem;
    

    @include respond(md) {
      display: none;
      line-height: 2rem;
    }
  }

}

