@use '../abstracts' as *;

.wrapper{
    width: 75%;
    margin: 2rem auto;
    @include respond(md){
        width: 85%; 
    }
}
.main_intro{
    @include respond(md){
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    } 
}
.grid-two-cols {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto;
    grid-gap: 2rem;
    grid-auto-flow: dense;
    @include respond(md){
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
  }
.grid-three-cols {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: auto;
    grid-gap: 1rem;
    grid-auto-flow: dense;
    @include respond(md){
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
  }
  .item-1-1{
    align-self: center;
    @include respond(md) {
        width: 100%;
      }
}
  .item-2-3{
      grid-column: span 2;
      align-self: center;
  
  }

  .item-3-3{
    grid-column: span 3;
    align-self: center;
}

  
  .container-flex{
      display: flex;
      @include respond(md){
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
      }
  
  }
  .intro * {
      @include respond(md){
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
      }
    
  }
