@use '../abstracts' as *;

.custom-btn {
  width: 13rem;
  height: 4rem;
  color: #fff;
  padding: 1rem 2.5rem;
  font-weight: 300;
  background: transparent;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
  //   this is necessary since I changed the button from a button to an a tag
  text-align: center;
  text-decoration: none;
  color: $white;
  //   this is necessary since I changed the button from a button to an a tag
  @include respond(md) {
    display: inline-block;
    margin-left: auto;
    margin-right: auto;
  }
}

/* 3 */
.btn-3 {
 background-color: $brand-color;
  width: 130px;
  height: 40px;
  line-height: 42px;
  padding: 0;
  border: none;
  @include respond(md) {
    width: 100px;
  }
}
.btn-3 span {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
  font-size: 1.1rem;
}
.btn-3:before,
.btn-3:after {
  position: absolute;
  content: '';
  right: 0;
  top: 0;
  background: $brand-color;
  transition: all 0.3s ease;
}
.btn-3:before {
  height: 0%;
  width: 2px;
}
.btn-3:after {
  width: 0%;
  height: 2px;
}
.btn-3:hover {
  background: transparent;
  box-shadow: none;
}
.btn-3:hover:before {
  height: 100%;
}
.btn-3:hover:after {
  width: 100%;
}
.btn-3 span:hover {
  color: $brand-color;
}
.btn-3 span:before,
.btn-3 span:after {
  position: absolute;
  content: '';
  left: 0;
  bottom: 0;
  background: $brand-color;
  transition: all 0.3s ease;
}
.btn-3 span:before {
  width: 2px;
  height: 0%;
}
.btn-3 span:after {
  width: 0%;
  height: 2px;
}
.btn-3 span:hover:before {
  height: 100%;
}
.btn-3 span:hover:after {
  width: 100%;
}