// ! MEDIA QUERY MANAGER

//? Break points
//? X-Small	None	<576px
//? Small	sm	≥576px
//? Medium	md	≥768px
//? Large	lg	≥992px
//? Extra large	xl	≥1200px
//? Extra extra large	xxl	≥1400px


// $breakpoint argument choices: 
//$ -x-sm
//$ -sm
//$ -md
//$ -lg
//$ -xl
//$ -xxl

//! max-width: “If [device width] is less than or equal to 600px, then do {…}”
//! min-width: “If [device width] is greater than or equal to 600px, then do {…}”

@mixin respond ($breakpoint){

    @if $breakpoint==x-sm {
        @media (max-width: 575px) {    
            @content
        } ;
  }

    @if $breakpoint==sm {
        @media (max-width: 576px) {    
            @content
        } ;
  }
  
  
  
    @if $breakpoint==md {
        @media (max-width: 768px) {    
            @content
        } ;
  }
  
  
  
    @if $breakpoint==lg {
        @media (max-width: 992px) {    
            @content
        } ;
  }
  
  
  
    @if $breakpoint==xl {
        @media (max-width: 1200px) {    
            @content
        } ;
  }

  @if $breakpoint==xxl {
    @media (max-width: 1400px) {    
        @content
    } ;
}
  
  
    @if $breakpoint==bigScreen {
        @media (min-width: 1400px) {    
            @content
        } ;
  }
  
  
  }

  //? Responsive Fonts
//  fontNav => Fonts as they appear in the navigation Bar
//  heroHeading => Fonts as they appear in the Hero section - main text

@mixin fontNav {
    font-size: clamp(.8rem, 1.2rem, 1.8rem);
  }
  @mixin heroHeading {
    font-size: clamp(1rem, 1.6rem, 2rem);
  }