@use '../abstracts' as *;

// -----------------------------------------------------------------------------
// This file contains very basic styles.
// -----------------------------------------------------------------------------
// @font-face {
//   font-family: 'Montserrat';
//   src: url('/src/assets/fonts/Montserrat-Regular.ttf');
//   font-display: swap;
// }

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  // This defines what 1rem is
  font-size: 62.5%;

  @include respond(xxl) {
    font-size: 100%;
    /*1 rem = 9px, 9/16 = 56.25%   --width<1200px */
  }

  @include respond(xl) {
    font-size: 90%;
    /*1 rem = 9px, 9/16 = 56.25%   --width<1200px */
  }

  @include respond(lg) {
    font-size: 80%;
    /*1 rem = 8px, 8/16 = 50%  --width<900px */
  }
  @include respond(md) {
    font-size: 65%;
    /*1 rem = 8px, 8/16 = 50%  --width<900px */
  }

  @include respond(bigScreen) {
    font-size: 110%;
    // 1.6rem
  }
}

body {
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
  color: $brand-color;
}
